/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */
import React, { Fragment, useState, useEffect } from "react";
import PropTypes from "prop-types";
import { useLocation } from "@reach/router";
import { useStaticQuery, graphql } from "gatsby";
import Helmet from "react-helmet";
import { utils } from "@core/helper";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { collectionKeys, getDocDataFilter } from "@core/helper/utils";

function Seo(props) {
  const {
    title,
    metaTitle: metaTitleProp,
    description,
    image,
    children,
    noIndex,
    ogTitle,
    ogDescription,
    ogType,
    twitterTitle,
    twitterDescription,
    siteFirestore,
    slug,
  } = props;
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            keywords
            author
            siteUrl
          }
        }
      }
    `
  );

  const [metaData, setMetaData] = useState({
    title: title,
    description: description,
    image: image,
    ogTitle: ogTitle,
    ogDescription: ogDescription,
    ogType: ogType,
    twitterTitle: twitterTitle,
    twitterDescription: twitterDescription,
  });
  console.log("metaData::", metaData);

  useEffect(() => {
    if (!siteFirestore) return;
    const app = initializeApp(siteFirestore);
    const db = getFirestore(app);
    const fetchData = async () => {
      try {
        const resp = await getDocDataFilter(
          db,
          collectionKeys.META,
          "slug",
          slug
        );
        console.log("Firebase SEO Data:", resp);

        if (resp) {
          const metaResp = resp?.[0]
          setMetaData({
            title: metaResp?.title,
            description: metaResp?.description,
            image: resp?.image,
            ogTitle: metaResp?.ogTitle,
            ogDescription: metaResp?.ogDescription,
            ogType: metaResp?.ogType,
            twitterTitle: metaResp?.twitterTitle,
            twitterDescription: metaResp?.twitterDescription,
          });
        }
      } catch (error) {
        console.error("Error fetching SEO data from Firestore:", error);
      }
    };
    if (slug) {
      fetchData();
    }
  }, [siteFirestore, slug]);

  const location = useLocation();
  const defaultTitle = site.siteMetadata?.title;
  const metaTitle = metaTitleProp || defaultTitle;
  const ogTitleAlt = ogTitle || metaTitle;
  const metaDescription = description || site.siteMetadata.description;
  const ogDescriptionAlt = ogDescription || metaDescription;
  const ogTypeAlt = ogType || "website";
  const ogImage = image;
  const twitterTitleAlt = twitterTitle || metaTitle;
  const twitterDescAlt = twitterDescription || metaDescription;
  const currentUrl = location?.href || site.siteMetadata.siteUrl;

  return (
    <Fragment>
      <Helmet>
        {/* <title>{metaData.title}</title>
        <meta name="description" content={metaData.description} /> */}
        <title>
          {metaData?.title ? metaData?.title : title ? title : defaultTitle}
        </title>
        <meta
          name="title"
          content={
            metaData?.title ? metaData?.title : title ? title : defaultTitle
          }
        />
        <meta
          name="description"
          content={
            metaData?.description ? metaData?.description : metaDescription
          }
        />
        <meta property="og:locale" content={"en_US"} />
        <meta
          property="og:title"
          content={metaData?.ogTitle ? metaData?.description : ogTitleAlt}
        />
        <meta
          property="og:description"
          content={
            metaData?.description ? metaData?.description : ogDescriptionAlt
          }
        />
        <meta
          property="og:image"
          content={site.siteMetadata.siteUrl + ogImage}
        />
        <meta
          property="og:type"
          content={metaData?.ogType ? metaData?.ogType : ogTypeAlt}
        />
        <meta property="og:url" content={currentUrl} />
        <meta name="twitter:card" content="summary" />
        <meta
          name="twitter:creator"
          content={site.siteMetadata?.author || ``}
        />
        <meta
          name="twitter:title"
          content={
            metaData?.twitterTitle ? metaData?.twitterTitle : twitterTitleAlt
          }
        />
        <meta
          name="twitter:description"
          content={
            metaData?.twitterDescription
              ? metaData?.twitterDescription
              : twitterDescAlt
          }
        />
        <meta
          name="robots"
          content={`${
            noIndex ? "noindex" : "index"
          }${", follow, max-image-preview:large, max-snippet:-1, max-video-preview:-1"}`}
        />
        {children}
      </Helmet>
    </Fragment>
  );
}

Seo.defaultProps = {
  description: ``,
};

Seo.propTypes = {
  description: PropTypes.string,
  title: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
};

export default Seo;
